.filme {
  margin: 10px;
  max-width: 500px;
  display: flex;
  justify-content: center;
;
  /* flex-direction: column; */
}
.filme .player-wrapper {
  padding: 10px;
  background-color:#fff;
  border-radius: 4px;
}
.filme .player-wrapper .react-player {
  margin-top: 0px;
  padding-top: 0px;
}

.filme .player-wrapper .detalheFilme{
  padding: 10px;
  border-radius: 4px;
  background-color: #fff;
}

.filme .player-wrapper .btnVoltar {
  background-color: #7159c1;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border: 0;
  margin-top: 12px;
  margin-bottom: 12px;
  height: 30px;
  cursor: pointer;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  letter-spacing: 1px;
  text-transform: uppercase;
  outline: none;
  border-radius: 3px;
  text-decoration: none;
}